import { Component, OnInit, Input } from '@angular/core';
import { Pipe, PipeTransform } from '@angular/core';

@Component({
  selector: 'app-json-viewer',
  templateUrl: './json-viewer.component.html',
  styleUrls: ['./json-viewer.component.css']
})
export class JsonViewerComponent implements OnInit {

  @Input() data:any;
  constructor() { }

  ngOnInit() {
  }
  clicked(e) {
    e.stopPropagation();
    const children = e.target.querySelector('ul') || (e.target.nextSibling && e.target.nextSibling.querySelector && e.target.nextSibling.querySelector('ul'));
    if (children && children.classList) {
      const classList = children.classList;
      if (classList.contains('open')) classList.remove('open');
      else classList.add('open');
    }
  }
}

@Pipe({name: 'type'})
export class Type implements PipeTransform {
  transform(value: any) {
    return Array.isArray(value) ? 'array' : typeof value;
  }
}
