import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import * as _ from 'lodash';
import { ApiService } from '../api.service';
@Component({
  selector: 'app-sidenav',
  templateUrl: './sidenav.component.html',
  styleUrls: ['./sidenav.component.css']
})
export class SidenavComponent implements OnInit {
  @Output() opened = new EventEmitter<object>();
  constructor(private Api: ApiService) { }
  dbs: any;
  ngOnInit() {
    this.Api.getDbs()
    .subscribe((res: any) => {
      this.dbs = res;
      this.filter();
    });
  }
  displayData;
  searchText = '';
  filter() {
    this.displayData = _.cloneDeep(this.dbs.databases);
    console.log(this.displayData)
    if (!this.searchText) return;
    const pattern = new RegExp(`.*${this.searchText}.*`, 'gi')
    this.displayData = this.displayData.map(db => {
      db.collections = db.collections.filter(col => pattern.test(col));
      return db;
    });
  }
  toggleDB(event) {
    event.preventDefault();
    if (event.target.classList.contains('open')) event.target.classList.remove('open');
    else event.target.classList.add('open');
  }
  openCollection(event) {
    event.preventDefault();
    const dbName = event.target.attributes['data-database'].value;
    const collectionName = event.target.innerText;
    if (dbName && collectionName) this.opened.emit({
      dbName,
      collectionName
    });
  }
}
